.hello {
    color: palevioletred;
}

.all {
    margin: 10px;
    border-bottom: 2px solid aqua;
}

header {
    width: 100%;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url('../imageContainer/bg.jpg') no-repeat;
    background-size: cover;
    background-color: rgb(71, 71, 71);
}

.profile {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

h2, p {
    font-family: Arial, Helvetica, sans-serif;
    color: #fafafa;
    margin: 0;
}

header h2 {
    margin-top: 10px;
    font-size: 30px;
}

header p {
    margin-top: 15px;
    font-size: 20px;
}

header button {
    cursor: pointer;
    margin-top: 10px;
    border: none;
    outline: none;
    border-radius: 50px;
    font-size: 16px;
    font-family: Arial, Helvetica, sans-serif;
    color: #fafafa;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
    background: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
    box-shadow: 0 0 10px #cf6cc9;
}

.avatar {
    margin-left: auto;
    margin-right: auto;
    width: 180px;
    height: 180px;
    border-radius: 50%;
    border: 10px solid rgb(240, 124, 240);
    background: url('./ava.jpg') no-repeat;
    background-size: cover;
}

nav {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.li {
    padding: 10px;
    margin: 10px;
    text-decoration: none;
    color: #fafafa;
    background: rgb(56, 56, 56);
    border-radius: 50px;
    font-family: Arial, Helvetica, sans-serif;
    border: 2px solid transparent;
}

.li.active {
   border: 2px solid rgb(233, 111, 233) 
}