* { box-sizing: border-box; }

/* force scrollbar */

body { }

/* ---- grid ---- */

.grid {
  background: #DDD;
}

/* clear fix */
.grid:after {
  content: '';
  display: block;
  clear: both;
}

/* ---- .grid-item ---- */

.grid-sizer,
.grid-item {
  width: 33.333%;
}

.grid-item {
  float: left;
  height: auto;
}

.grid-item img {
  display: block;
  max-width: 100%;
  height: auto;
}
